/**
 * Helper to make it easier to print font families
 *
 * @param $fontfamily {string}  The desired family from the map.
 *
 * @return string               The requested fontsize index
 */
@function font-family( $index: base ) {

    $map-font-family: (
        base: serif
    );

    @if map-has-key($map-font-family, $index) {

        @return map-get($map-font-family, $index);

    } @else {

        @warn '`#{index}` is not a valid index in Map: $map-font-family.';
        @return null;

    }

}